<template>
  <div class="management_content">
    <div class="management_list_content">
      <div class="management_top_search">
        <div>
          <span style="color:rgb(64, 64, 65);">Client Name:</span>
          <el-select v-model="client_search" placeholder="No Client Selected" @change="currentSel">
            <el-option
              v-for="item in client_options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>

          <span class="project_content" v-if="this.project_title !==''">Project:</span>
          <span>{{project_title}}</span>
        </div>
      </div>
    </div>
    <div class="address_table_content" style="color:rgb(64, 64, 65);">
      <el-table :data="tableData" id="manager_bg">
        <el-table-column type="index" label="No." align="center"></el-table-column>

        <el-table-column label="Status" align="center" width="130">
          <template slot-scope="scope">

              <!-- <span v-if="tableData[scope.$index].status==='Validated'" style="color:green;">{{tableData[scope.$index].status}}</span>

              <span v-else-if="tableData[scope.$index].status==='Validation Failed'" style="color:rgb(163, 32, 32);">{{tableData[scope.$index].status}}</span>

              <span v-else style="color:rgb(254, 250, 245);">{{tableData[scope.$index].status}}</span> -->

              <div v-if="tableData[scope.$index].status==='Validated'" style="width:100%;background:green;color:rgb(254, 250, 245);">{{tableData[scope.$index].status}}</div>

              <div v-else-if="tableData[scope.$index].status==='Validation Failed'" style="width:100%;background:rgb(163, 32, 32);color:rgb(254, 250, 245);">{{tableData[scope.$index].status}}</div>

              <div v-else style="width:100%;background-color:#EB8C00;color:rgb(254, 250, 245); ">{{tableData[scope.$index].status}}</div>

            </template>
        </el-table-column>

        
        <el-table-column prop="currency" label="Currency" align="center"></el-table-column>
        <el-table-column prop="address" label="Public Address" align="center"></el-table-column>
        <el-table-column prop="ownership" label="Ownership" align="center"></el-table-column>
        <el-table-column prop="public_key" label="Public Key" align="center"></el-table-column>
        <el-table-column label="Multisig(M/N)" align="center">
          <template slot-scope="scope">

            <span v-if="tableData[scope.$index].multi_signature===true">
              <span>{{tableData[scope.$index].m}}</span>
              <span>/</span>
              <span>{{tableData[scope.$index].n}}</span>
            </span>

            <span v-else>
              <i style="display:block;margin:auto; width:20px;height:1px;background-color:rgb(64, 64, 65)"></i>
            </span>

          </template>
        </el-table-column>
        <el-table-column label="Operation" align="center">
          <template slot-scope="scope">
            <div id="edit_address_content">
              <el-button
                type="warning"
                size="mini"
                icon="el-icon-edit"
                v-if="tableData[scope.$index].status=='No Message'"
                @click="editAddress(scope.$index, scope.row)"
              ></el-button>
              <el-button type="warning" size="mini" icon="el-icon-edit" v-else disabled></el-button>
              <el-dialog
                title="Edit Address"
                :visible.sync="editDialogVisible"
                :before-close="handleClose"
              >
                <el-form
                  :model="editAddressData"
                  label-width="150px"
                  ref="EditAddress"
                  align="left"
                >
                 
                  <el-form-item label="Currency">
                    <el-select v-model="editAddressData.currency" disabled>
                      <el-option
                        v-for="item in currency_option"
                        :key="item"
                        :label="item"
                        :value="item"
                      ></el-option>
                    </el-select>
                  </el-form-item>

                   <el-form-item label="Address">
                    <el-input v-model="editAddressData.address" autocomplete="off" disabled></el-input>
                  </el-form-item>
                  

                  <el-form-item label="Ownership">
                    <el-select v-model="editAddressData.ownership" placeholder="Choose ownership">
                      <el-option
                        v-for="item in ownership_option"
                        :key="item"
                        :label="item"
                        :value="item"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="Multisig">
                    <el-switch v-model="editAddressData.multi_signature"></el-switch>
                  </el-form-item>

                  <el-form-item
                    v-if="editAddressData.multi_signature === true"
                    id="addressEdit"
                    label="M/N"
                  >
                    <el-input v-model="editAddressData.m" autocomplete="off" placeholder="M"></el-input>
                    <span>/</span>
                    <el-input v-model="editAddressData.n" autocomplete="off" placeholder="N"></el-input>
                  </el-form-item>
                
                  <div class="warning_message">{{edit_message}}</div>
                  <div class="add_btn_content">
                    <el-button size="mini" @click="handleClose()" class="consist_black">Cancel</el-button>
                    <el-button
                      size="mini"
                      type="primary"
                      @click="saveEdit()"
                      class="consist_white"
                    >Save</el-button>
                  </div>
                </el-form>
              </el-dialog>
              <el-button
                type="primary"
                size="mini"
                icon="el-icon-delete"
                v-if="tableData[scope.$index].status=='No Message'"
                @click="deleteDialogVisible=true"
              ></el-button>
              <el-button type="primary" size="mini" icon="el-icon-delete" v-else disabled></el-button>
              <el-dialog title="Delete Address" :visible.sync="deleteDialogVisible" width="30%">
                <span>Are you sure to delete</span>
                <br>
                <span>currency:
                  <span>{{scope.row.currency}}</span>
                </span>
                <br>
                <span>{{scope.row.address}}</span>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="deleteDialogVisible = false" class="consist_black">Cancel</el-button>
                  <el-button
                    type="primary"
                    @click="deleteAddress(scope.row.currency,scope.row.address)"
                    class="consist_white"
                  >Delete</el-button>
                </span>
              </el-dialog>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="management_button_content" v-if="project_title !== ''">
      <div class="add_address_content">
        <el-button
          type="primary"
          size="small"
          icon="el-icon-circle-plus-outline"
          @click="addDialogVisible = true"
          class="consist_white"
        >Add Address</el-button>
        <el-dialog
          title="Add Address"
          :visible.sync="addDialogVisible"
          :before-close="handleClose"
          align="center"
        >
          <el-form :model="addAddressData" label-width="150px" ref="addNewAddress">

             <el-form-item label="Currency">
              <el-select v-model="addAddressData.currency" @change="currencySel">
                <el-option v-for="item in currency_option" :key="item" :label="item" :value="item"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="Address">
              <el-input
                v-model="addAddressData.address"
                style="color: #909399;"
                placeholder="Public Address"
              ></el-input>
            </el-form-item>

            <el-form-item label="Public Key" v-if="value_xrp==='Ripple(XRP)'">
              <el-input
                v-model="addAddressData.public_key"
                style="color: #909399;"
                placeholder="Public Key"
              ></el-input>
            </el-form-item>
  
            <el-form-item label="Ownership">
              <el-select
                v-model="addAddressData.ownership"
                style="color: #909399;"
                placeholder="Choose ownership"
              >
                <el-option v-for="item in ownership_option" :key="item" :label="item" :value="item"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="Multisig">
              <el-switch v-model="addAddressData.multi_signature"></el-switch>
            </el-form-item>
            <el-form-item
              id="addressAdd"
              v-if="addAddressData.multi_signature === true"
              label="M/N"
            >
              <el-input v-model="addAddressData.m" autocomplete="off" style="color: #909399;" placeholder="M"></el-input>
              <span>/</span>
              <el-input v-model="addAddressData.n" autocomplete="off" style="color: #909399;" placeholder="N"></el-input>
            </el-form-item>
            <div class="warning_message">{{add_message}}</div>
            <div class="add_btn_content">
              <el-button size="mini" @click="handleClose()" class="consist_black">Cancel</el-button>
              <el-button size="mini" type="primary" @click="addAddress()" class="consist_white">Add</el-button>
            </div>
          </el-form>
        </el-dialog>
      </div>
      <div class="upload_address_content">
        <el-upload
          ref="upload"
          accept=".xlsx"
          action="/cryptotool/address/upload"
          :auto-upload="true"
          :limit="1"
          :http-request="uploadAddress"
        >
          <el-button type="primary" size="small" class="consist_white">Import Addresses
            <i class="el-icon-upload el-icon--right"></i>
          </el-button>
        </el-upload>
      </div>
      <div class="download_template_content">
        <el-button type="text" @click="downExcel" id="down_link">Download the Excel template</el-button>
      </div>
    </div>
    <br>
    <br>

    <div class="address_log_content" v-if="(project_title !== '') && (record_data != null)">
      <hr>
      <h2>Operation Log</h2>
      <el-table
        :data="record_data"
        :default-sort="{prop: 'date', order: 'descending'}"
        style="width:100%;color:rgb(64, 64, 65);"
      >
        <el-table-column prop="date" width="160"></el-table-column>
        <el-table-column prop="operator" width="200"></el-table-column>
        <el-table-column prop="detail" width="800"></el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import apiRequest from "../request/request";
import store from "../store/store";
export default {
  data() {
    return {
      addDialogVisible: false,
      deleteDialogVisible: false,
      editDialogVisible: false,

      tableData: [],
      client_search: "",
      project_title: "",
      client_name: "",
      client_options: [],
      addAddressData: {
        address: "",
        currency: "",
        multi_signature: false,
        m: 0,
        n: 0,
        ownership: "",
        public_key:""
      },
      editAddressData: {
        address: "",
        currency: "",
        multi_signature: false,
        m: 0,
        n: 0,
        ownership: ""
      },
      add_message: "",
      edit_message: "",
      record_data: [],

      edit_ownership: "",

      value_xrp:""
    };
  },

  mounted() {
    if (store.readToken() === "") {
      this.getUserName();
    }
    var username = store.readUserName();
    if (username === "") {
      store.deleteUserName();
      store.deleteToken();
      this.$router.push({ name: "user" });
      return;
    }

    if (this.client_search === "") {
      this.client_name = "";
      this.project_title = "";
    }
    this.clientView();
    this.currencyView();
    this.ownershipView();
  },

  methods: {
    getUserName() {
      let global = this;
      apiRequest
        .sendRequest("get", "/cryptotool/user/current/get")
        .then(response => {
          if (response) {
            if (response.code === "0") {
              store.saveUserName(response.data.userName);
            }
          }
        });
    },
    clientView() {
      let global = this;
      apiRequest
        .sendRequest("post", "/cryptotool/user/clients/view")
        .then(function(response) {
          if (response) {
            if (response.code === "0" && response.data.clientList != null) {
              for (var i = 0; i < response.data.clientList.length; i++) {
                var obj = {};
                obj.value = i;
                obj.label = response.data.clientList[i].clientName;
                obj.projecName = response.data.clientList[i].project_title;
                global.client_options.push(obj);
              }
            } else {
              global.$message.warning(
                "service exceptionalplease contract with manager!"
              );
            }
          }
        });
    },
    currencyView() {
      let global = this;
      apiRequest
        .sendRequest("get", "/cryptotool/info/curreny/view")
        .then(function(response) {
          if (response) {
            if (response.code === "0") {
              global.currency_option = response.data.currencies;
              global.addAddressData.currency=global.currency_option[0];

            } else {
              global.$message.warning(
                "service exceptionalplease contract with manager!"
              );
            }
          }
        });
    },
    ownershipView() {
      let global = this;
      apiRequest
        .sendRequest("get", "/cryptotool/info/ownership/view")
        .then(function(response) {
          if (response) {
            if (response.code === "0") {
              global.ownership_option = response.data.ownerships;
            } else {
              global.$message.warning(
                "service exceptionalplease contract with manager!"
              );
            }
          }
        });
    },
    addressView() {
      let global = this;
      apiRequest
        .sendRequest(
          "post",
          "/cryptotool/address/view",
          JSON.stringify({
            clientName: global.client_name,
            projectTitle: global.project_title
          })
        )
        .then(function(response) {
          if (response) {
            if (response.code === "0") {
              global.tableData = response.data.addresslist;
              console.log(global.tableData);
            } else if (response.code === "10001" || response.code === "10005") {
              global.$message.warning("no data");
            } else {
              global.$message.warning(response.msg);
            }
          }
        });
    },
    currentSel(index) {
      this.tableData = [];
      this.project_title = this.client_options[index].projecName;
      this.client_name = this.client_options[index].label;
      this.addressView();
      this.addressLogView();
    },

    currencySel(val){
      console.log(val);
      console.log(typeof(val));

      this.value_xrp=val;

    },
    handleClose() {
      this.add_message = "";
      this.edit_message = "";

      this.addDialogVisible = false;
      this.editDialogVisible = false;

      this.addAddressData.address = "";
      this.addAddressData.currency = this.currency_option[0];
      this.addAddressData.multi_signature = false;
      this.addAddressData.m = "";
      this.addAddressData.n = "";
      this.addAddressData.ownership = "";
      this.addAddressData.public_key="";

    
      this.editAddressData.multi_signature = false;
      this.editAddressData.m = "";
      this.editAddressData.n = "";
      this.editAddressData.ownership = this.edit_ownership;
    },
    addAddress() {
      if (
        this.addAddressData.address == "" ||
        this.addAddressData.ownership == ""
      ) {
        this.add_message = "Information cannot be empty!";
        return;
      }
      if( (this.value_xrp==='Ripple(XRP)') && (this.addAddressData.public_key=="")){
        
        this.add_message = "Information cannot be empty!";

        return;
      }
      if (
        this.addAddressData.multi_signature == true &&
        (this.addAddressData.m == 0 || this.addAddressData.n == 0)
      ) {
        this.add_message = "Information cannot be empty!";
        return;
      }

      if (this.addAddressData.multi_signature == true) {
        this.addAddressData.m = parseFloat(this.addAddressData.m);
        this.addAddressData.n = parseFloat(this.addAddressData.n);

        var pattern = /^[1-9]\d*$/;
        if (
          !pattern.test(this.addAddressData.m) ||
          !pattern.test(this.addAddressData.n)
        ) {
          this.add_message = "M/N must be a positive integer!";
          return;
        }
        if (
          pattern.test(this.addAddressData.m) &&
          pattern.test(this.addAddressData.n)
        ) {
          if (this.addAddressData.m > this.addAddressData.n) {
            this.add_message = "M must be less than N!";
            return;
          }
          if (this.addAddressData.n > 16) {
            this.add_message = "N must be not greater than 16!";
            return;
          }
        }
      }
      if (this.addAddressData.multi_signature == false) {
        this.addAddressData.m = "";
        this.addAddressData.n = "";
      }
      let global = this;
      apiRequest
        .sendRequest(
          "post",
          "/cryptotool/address/add",
          JSON.stringify({
            clientName: global.client_name,
            projectTitle: global.project_title,
            AddressInfo: {
              address: global.addAddressData.address,
              currency: global.addAddressData.currency,
              ownership: global.addAddressData.ownership,
              multi_signature: global.addAddressData.multi_signature,
              public_key:this.addAddressData.public_key,
              m: parseInt(global.addAddressData.m),
              n: parseInt(global.addAddressData.n)
              
            }
          })
        )
        .then(function(response) {
          if (response) {
            if (response.code === "0") {
              global.$message.success(response.msg);
              global.handleClose();
              global.addressView();
            } else if (response.code === "20013") {
              global.add_message = "the address has existed";
            } else {
              global.add_message = response.msg;
            }
          }
        });
      global.addressLogView();
    },
    deleteAddress(currency, address) {
      let global = this;
      apiRequest
        .sendRequest(
          "post",
          "/cryptotool/address/delete",
          JSON.stringify({
            clientName: global.client_name,
            projectTitle: global.project_title,
            address: address,
            currency: currency
          })
        )
        .then(function(response) {
          if (response) {
            if (response.code === "0") {
              global.$message.success(response.msg);
              global.addressView();
            } else {
              global.$message.warning(response.msg);
            }
          }
        });
      global.deleteDialogVisible = false;
      global.addressLogView();
    },
    editAddress(index, row) {
      this.edit_ownership = row.ownership;
      this.editDialogVisible = true;
      this.editAddressData = row;
    },
    saveEdit() {
      if (
        this.editAddressData.address == "" ||
        this.editAddressData.currency == "" ||
        this.editAddressData.ownership == ""
      ) {
        this.edit_message = "Information cannot be empty!";
        return;
      }
      if (
        this.editAddressData.multi_signature == true &&
        (this.editAddressData.m == "" || this.editAddressData.n == "")
      ) {
        this.edit_message = "Information cannot be empty!";
        return;
      }
      if (this.editAddressData.n > 16) {
        this.edit_message = "N must be not greater than 16!";
        return;
      }

      if (this.editAddressData.multi_signature == true) {
        this.editAddressData.m = parseFloat(this.editAddressData.m);
        this.editAddressData.n = parseFloat(this.editAddressData.n);

        var pattern = /^[1-9]\d*$/;
        if (
          !pattern.test(this.editAddressData.m) ||
          !pattern.test(this.editAddressData.n)
        ) {
          this.edit_message = "M/N must be a positive integer!";
          return;
        }
        if (
          pattern.test(this.editAddressData.m) &&
          pattern.test(this.editAddressData.n)
        ) {
          if (this.editAddressData.m > this.editAddressData.n) {
            this.edit_message = "M must be less than N!";
            return;
          }
        }
      }
      if (this.editAddressData.multi_signature == false) {
        this.editAddressData.m = "";
        this.editAddressData.n = "";
      }

      let global = this;
      apiRequest
        .sendRequest(
          "post",
          "/cryptotool/address/edit",
          JSON.stringify({
            clientName: global.client_name,
            projectTitle: global.project_title,
            addressInfo: {
              address: global.editAddressData.address,
              currency: global.editAddressData.currency,
              ownership: global.editAddressData.ownership,
              multi_signature: global.editAddressData.multi_signature,
              m: parseInt(global.editAddressData.m),
              n: parseInt(global.editAddressData.n)
            }
          })
        )
        .then(function(response) {
          if (response) {
            if (response.code === "0") {
              global.$message.success(response.msg);
              global.handleClose();
              global.addressView();
              global.editDialogVisible=false;
            } else {
              global.edit_message = response.msg;
            }
          }
        });
      global.addressLogView();
    },
    uploadAddress(param) {
      var fileObj = param.file;
      var formData = new FormData();
      formData.append("file", fileObj);
      formData.append("clientName", this.client_name);
      formData.append("projectTitle", this.project_title);

      let global = this;
      apiRequest
        .UploadRequest("post", "/cryptotool/address/upload", formData)
        .then(function(response) {
          if (response) {
            if (response.code === "0") {
              global.$message.success(response.msg);
            } else {
              global.$message.warning(response.msg);
            }
            global.$refs.upload.clearFiles();
            global.addressLogView();
            global.addressView();
          }
        });
    },

    downExcel() {
      let global = this;
      apiRequest.DownloadRequest("get","/cryptotool/address/template/download",null,null,"Inventory List.xlsx")
        .then(function(response) {
          if(response){
            global.$message.warning(response.msg)
          }
         // window.location.href = "/cryptotool/address/template/download";
        });
      global.addressLogView();
    },

    addressLogView() {
      let global = this;
      apiRequest
        .sendRequest(
          "post",
          "/cryptotool/log/address/view",
          JSON.stringify({
            clientName: global.client_name,
            projectTitle: global.project_title
          })
        )
        .then(function(response) {
          if (response) {
            if (response.code === "0") {
              global.record_data = response.data;
              if (global.record_data != null) {
                global.record_length = global.record_data.length;
                for (var i = 0; i < global.record_length; i++) {
                  global.year = global.record_data[i].date.slice(0, 4);
                  global.month = global.record_data[i].date.slice(4, 6);
                  global.day = global.record_data[i].date.slice(6, 8);
                  global.hour = global.record_data[i].date.slice(8, 10);
                  global.miniter = global.record_data[i].date.slice(10, 12);
                  global.seconds = global.record_data[i].date.slice(12, 14);
                  global.record_data[i].date =
                    global.year +
                    "-" +
                    global.month +
                    "-" +
                    global.day +
                    " " +
                    global.hour +
                    ":" +
                    global.miniter +
                    ":" +
                    global.seconds;
                  //this.record_data[i].date=this.record_data[i].date.slice(0,19);
                }
              } else {
                global.record_length = 0;
              }
            } else {
              global.$message.warning(response.msg);
            }
          }
        });
    }
  }
};
</script>

<style>
/*  */

.el-textarea__inner {
  color: #909399;
  font-family: georgia, serif;
}

/*  */
.management_content {
  width: 80%;
  min-width: 800px;
  margin: auto;
  font-family: georgia, serif !important;
}
.management_top_search {
  margin-top: 20px;
  padding-bottom: 10px;
}
.management_list_content {
  margin: auto;
}
#down_link span {
  display: block;
  margin-top: 10px;
}
.management_button_content {
  padding: 10px;
}
.project_content {
  color: rgb(64, 64, 65);
  padding-left: 20px;
}
.add_address_content {
  float: left;
  padding-right: 10px;
}
.add_address_content label,
#edit_address_content label {
  
}
.add_address_content .el-dialog,
#edit_address_content .el-dialog {
  width: 40%;
  min-width: 500px;
}
.add_address_content .el-input,
#edit_address_content .el-input {
  width: 80%;
  float: left;
  /* border: 1px red solid; */
}
.add_address_content .el-select,
#edit_address_content .el-select {
  width: 100%;
  float: left;
  /* border: 1px black solid; */

}
.add_address_content .el-switch,
#edit_address_content .el-switch {
  float: left;
  display: block;
  margin-top: 10px;
}
#addressAdd .el-input,
#addressEdit .el-input {
  width: 15%;
  float: left;
}
#addressAdd span,
#addressEdit span {
  float: left;
  font-size: 24px;
  font-weight: 500;
  margin: 0 5px 0 8px;
}
.address_log_content .el-table__header-wrapper {
  display: none;
}

.upload_address_content {
  float: left;
  padding-right: 5px;
}
.download_template_content {
  vertical-align: bottom;
}
.download_template_content .el-button--text {
  font-size: 14px;
  color: #a32020;
  text-decoration: underline;
}
.el-switch {
  vertical-align: bottom;
}
.add_btn_content {
  text-align: center;
}
.warning_message {
  width: 100%;
  height: 40px;
  font-size: 16px;
  color: red;
  text-align: center;
  padding: 5px;
}

.address_log_content {
  text-align: center;
  margin-bottom: 30px;
}
.address_log_content h2 {
  color: rgb(64, 64, 65);
  height: 80px;
  line-height: 80px;
}
.address_log_content td {
  border: 0;
  padding: 4px 0;
}
.address_log_content .has-gutter {
  display: none;
}
.consist_white {
  color: rgb(254, 250, 245);
}
.consist_black {
  color: rgb(64, 64, 65);
}
/* #manager_bg .el-table__body-wrapper td:nth-child(2) .cell{
 background-color:#EB8C00; 
} */
</style>



