import ClientRequest from './client'
import store from '../store/store'
import router from '../router/index.js'
var CryptoJS = require("crypto-js");


export default {
    sendRequest(method,path,params,headers){

        var hash = CryptoJS.HmacSHA256(params, "pwc");
        var hashInBase64 = CryptoJS.enc.Base64.stringify(hash);

        var global = this
        const reqConfig = {
            method: method,
            url:path,
            headers: Object.assign(
                {
                    "Content-Type": "application/json",
                    "X-Token":store.readToken(),
                    "X-Frame-Options":"deny",
                    "hmac":hashInBase64,
                },
                headers
            )
        };
        if (method === 'post'|| method === 'patch'){
            reqConfig.data = params;
        } else {
            reqConfig.params = params;
        }
    
        return ClientRequest(reqConfig).then(response=>{

            if (response.status && response.status === 200 && response.data.code !== undefined) {
                if (response.data.code === "10003" || response.data.code === "10006") {
                    
                    // global.$router.push({ name: "user" });
                    
                    store.deleteUserName()
                    store.deleteToken()
                    router.replace({
                    name: 'user'
                    })
                    global.$message.warning(response.data.msg);
                    return
                }

                return response.data;
            } else if(response.status && response.status !== 200){
                global.$message.error("("+error.response.status+") unexcepted service errorplease contact administrator!")
                return;
            }
            return
            
        }).catch(error=>{
            //TODO:
        });
    },
    UploadRequest(method,path,params,headers){
        var hash = CryptoJS.HmacSHA256(params, "pwc");
        var hashInBase64 = CryptoJS.enc.Base64.stringify(hash);

        var global = this
        const reqConfig = {
            method: method,
            url:path,
            headers: Object.assign(
                {
                    "Content-Type": "multipart/form-data",
                    "X-Token":store.readToken(),
                    "X-Frame-Options":"deny",
                    "hmac":hashInBase64,
                },
                headers
            ),
            data:params,
        };
        return ClientRequest(reqConfig).then(response=>{
    
            if (response.status && response.status === 200 && response.data.code !== undefined) {
                if (response.data.code === "10003" || response.data.code === "10006") {
                    
                    global.$router.push({ name: "user" });
                    global.$message.warning(response.data.msg);
                    store.deleteUserName()
                    store.deleteToken()
                    return
                }
                return response.data;
            } else if(response.status && response.status !== 200){
                global.$message.error("("+error.response.status+") unexcepted service errorplease contact administrator!")
                return;
            }
            return
            
        }).catch(error=>{
            //TODO:
        });
    },
    DownloadRequest(method,path,params,headers,filename){

        var hash = CryptoJS.HmacSHA256(params, "pwc");
        var hashInBase64 = CryptoJS.enc.Base64.stringify(hash);

        var global = this
        const reqConfig = {
            method: method,
            url:path,
            headers: Object.assign(
                {
                    "Content-Type": "multipart/form-data",
                    "X-Token":store.readToken(),
                    "X-Frame-Options":"deny",
                    "hmac":hashInBase64,
                },
                headers
            ),
            data:params,
            responseType: "blob"
        };
        return ClientRequest(reqConfig).then(response=>{
    
            if (response.status && response.status === 200 ) {
                
                if (response.data.type === "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" || response.data.type === "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet; charset=UTF-8") {
                    
                    const blob = new Blob([response.data]);
      
                   if (window.navigator.msSaveOrOpenBlob) {
                      // IE10
                      navigator.msSaveBlob(blob, filename)
                    } else{
                        
                      // let fileName = response.headers["content-disposition"].split("=")[1];
                          // 
                          let objectUrl = URL.createObjectURL(blob);
                          // 
                          const link = document.createElement("a");

                          link.href = objectUrl;

                          link.download = filename;

                          document.body.appendChild(link);

                          link.click();

                          setTimeout(function(){document.body.removeChild(link)},1000);

                          global.MessageLogView();
                    }
                } else if (response.data.type ==="application/json"){
                    // var blob = new Blob([response.data], {type : "application/json"});
                    // var reader = new FileReader();
                    // reader.readAsBinaryString(blob);
                    // reader.addEventListener("loadend", function() {
                    // });
                    
                    global.$message.warning("download error!")
                   
                } else {
                    global.$message.error("("+error.response.status+") unexcepted service errorplease contact administrator!")
                }
            } else if(response.status && response.status !== 200){
                global.$message.error("("+error.response.status+") unexcepted service errorplease contact administrator!")
                return;
            }
            return
            
        }).catch(error=>{
            //TODO:
        });
    }
}


