<template>
	<div id="business" @click="clicked">
    	<div class="nav_head">
            <div class="head_ul">

            <el-menu
                :default-active="activeIndex"
                mode="horizontal"
                @select="handleSelect"
                background-color="#a32020"
                text-color="rgb(254, 250, 245)"
                id="header">
                    <el-menu-item index="1">Client Management</el-menu-item>
                    <el-menu-item index="2">Address Management</el-menu-item>
                    <el-menu-item index="3">Address Validation</el-menu-item>
            </el-menu>

            </div>
        </div>

        <router-view></router-view>
        

    </div>
</template>
<script>
import store from '../store/store'
export default {
    name:'home',
    data() {
        return {
            activeIndex: '0',
            lTime: new Date().getTime(), // 
            ctTime: new Date().getTime(), //
            tOut: 8 * 60 * 60 * 1000,   
            myVar:null,
        }
    },
    watch: {
        '$route' (to, from) {
            if(to.path==="/cryptoaudit/home/introduction"){
                // this.$router.go(0);
                // location.reload();
               //this.$router.push({ name: "introduction" });
                this.activeIndex ="0";
                //document.getElementsByClassName(".el-menu-item").style.background="";
            
            }
            if(to.path==="/cryptoaudit/home/clientlist"){
                    this.activeIndex="1";

            }
            if(to.path==="/cryptoaudit/home/address/manage"){
                    this.activeIndex="2";
            }
            if(to.path==="/cryptoaudit/home/address/validate"){
                    this.activeIndex="3";
            }
            
        }
    },
    mounted(){

        if(store.readToken()===null){
            if(store.readUserName()!==null){
                store.deleteUserName()
            }
            this.$router.push({ name: "user" });
            return     
        }
        var username = store.readUserName()
        if(username ===''){
            this.getUserName()
        }
        this.myVar = window.setInterval(this.tTime, 1000)
    },
    methods: { 
        getUserName(){
            let global = this;
            apiRequest.sendRequest("get","/cryptotool/user/current/get").then(response=> {
                if(response){
                    if (response.code === "0") {
                        store.saveUserName(response.data.userName)
                    }
                }
            });
        },

        handleSelect(key, keyPath) {
            if(key==="1"){
                this.$router.push({ name: "clientlist" });
            }
            if(key==="2"){
                this.$router.push({ name: "manageaddress" });
            }
            if(key==="3"){
                this.$router.push({ name: "validateaddress" });
            }

        },
        tTime() {
            this.ctTime = new Date().getTime()
            if (this.ctTime -this.lTime > this.tOut) {
                this.$router.push({ name: "user" });
                window.clearInterval(this.myVar)
            }
        },
        clicked() {
            this.lTime = new Date().getTime()  
        }, 
    }
}
</script>

<style>
    body{
        padding: 0;
        margin: 0;
    }
    .nav_head{
        background-color: rgb(163, 32, 32);
        /* border: 1px #909399 solid; */
        box-shadow: 0 2px 0 #bcbfc5;
        height:50px;
        /* border-bottom: 1px; */
        /* box-shadow: h-shadow v-shadow blur spread color inset; */
    }
    .nav_head ul{
        margin: 0;
    }
    .head_ul{
        width: 80%;
        min-width:800px;
        margin: auto;
        box-shadow: 0 2px 0 #bcbfc5;
    }

    .head_ul .el-menu{
        border: 0;
    }
    #header .el-menu-item{
        height: 50px;
        line-height: 50px;
    }
    .el-menu-item {
        font-family: georgia,serif;
        font-size: 1.125rem;
        /* height: 50px; */
        /* line-height: 50px; */
    }
    .el-menu--horizontal > .el-menu-item.is-active{
        background: #EB8C00 !important;
        color:rgb(254, 250, 245) !important;
        border-bottom-width: 0px;
    }
    #introduction_index{
        width: 0;
        height: 0;
        padding: 0;
    }
</style>


